import { RegisterComponent } from '@/common/register-component';
import { NylasSchedulerConfigConnector } from '@/connector/nylas-scheduler-config-connector';
import { debug } from '@/utils/utils';
import { AttachInternals, Component, Host, State, h, Element, Prop, Watch, Event, EventEmitter, Listen } from '@stencil/core';
import { NylasSchedulerEditor } from '../nylas-scheduler-editor/nylas-scheduler-editor';
import { Configuration } from '@nylas/core';

/**
 * The `nylas-location-component` component is a form input for the description of an event.
 * @part nel - The event location container
 * @part nel__location - The event location input
 * @part nel__dropdown - The dropdown container
 * @part nel__dropdown-button - The dropdown button
 * @part nel__dropdown-content - The dropdown content
 */
@Component({
  tag: 'nylas-location-component',
  styleUrl: 'nylas-location-component.scss',
  shadow: true,
  formAssociated: true,
})
export class NylasLocationComponent {
  /*
   * The host element <nylas-location-component>
   */
  @Element() host!: HTMLElement;
  /**
   * The name of the event location input.
   */
  @Prop() name: string = 'location';
  /**
   * The selected config
   */
  @Prop() selectedConfiguration?: Configuration;

  /**
   * The event location stored in the configuration
   */
  @Prop() eventLocation?: string;
  /**
   * The selected event location state. This defaults to the event location from the configuration or an empty string.
   */
  @State() location: string = this.eventLocation ?? '';
  /**
   * The selected location option. This defaults to 'none'.
   */
  @State() selectedLocationOption: string = this.eventLocation ? 'custom' : 'none';
  /**
   * The element internals.
   */
  @AttachInternals() internals!: ElementInternals;

  /**
   * When a name prop is passed, stencil does not automatically set the name attribute on the host element.
   * Since this component is form-associated, the name attribute is required for form submission.
   * This is a workaround to ensure that the name attribute is set on the host element.
   */
  @Watch('name')
  elementNameChangedHandler(newValue: string) {
    debug('nylas-location-component', 'elementNameChangedHandler', newValue);
    this.host.setAttribute('name', newValue);
  }

  @Watch('selectedConfiguration')
  selectedConfigurationChangedHandler(newValue: Configuration, oldValue: Configuration) {
    debug('nylas-custom-booking-flow', 'selectedConfigurationChangedHandler', newValue, oldValue);
    const configLocation = newValue?.event_booking?.location;
    if (configLocation) {
      this.selectedLocationOption = configLocation ? 'custom' : 'none';
      this.location = configLocation;
    }
  }

  @Watch('eventLocation')
  eventLocationChangedHandler(newValue: string, oldValue: string) {
    debug('nylas-location-component', 'eventLocationChangedHandler', newValue);
    if (newValue === oldValue) {
      return;
    }
    this.selectedLocationOption = newValue ? 'custom' : 'none';
  }

  @Event() valueChanged!: EventEmitter<{
    value: string;
    name: string;
  }>;

  connectedCallback() {
    debug('nylas-location-component', 'connectedCallback');
  }

  componentWillLoad() {
    debug('nylas-location-component', 'componentWillLoad');
    this.host.setAttribute('name', this.name);
  }

  componentDidLoad() {
    debug('nylas-location-component', 'componentDidLoad');
  }

  disconnectedCallback() {
    debug('nylas-location-component', 'disconnectedCallback');
  }

  @Listen('selectedOptionChanged')
  selectedOptionChangedHandler(event: CustomEvent<{ value: string }>) {
    this.selectedLocationOption = event.detail.value;
    if (this.selectedLocationOption === 'none') {
      this.location = '';
      this.setFormValue('', 'location');
      this.valueChanged.emit({ value: '', name: this.name });
    }
  }

  setFormValue(value: string, name: string) {
    if (typeof this.internals.setFormValue === 'function') {
      this.internals.setFormValue(value, name);
    }
  }

  handleChange(event) {
    this.location = event.target.value;
    this.setFormValue(event.target.value, 'location');
    this.valueChanged.emit({ value: event.target.value, name: this.name });
  }

  @RegisterComponent<NylasLocationComponent, NylasSchedulerConfigConnector, Exclude<NylasSchedulerEditor['stores'], undefined>>({
    name: 'nylas-location-component',
    stateToProps: new Map([['schedulerConfig.selectedConfiguration', 'selectedConfiguration']]),
    eventToProps: {},
    fireRegisterEvent: true,
  })
  render() {
    const locationOptions = [
      { value: 'none', label: 'None' },
      { value: 'custom', label: 'Custom location' },
    ];
    const icons = {
      none: <location-off-icon />,
      custom: <location-icon />,
    };

    return (
      <Host>
        <div class="nylas-event-location" part="nel">
          <label htmlFor="location">
            Location type
            <span class="label-icon">
              <tooltip-component>
                <info-icon slot="tooltip-icon" />
                <span slot="tooltip-content">
                  Choose how youâ€™ll meet with the participants at the scheduled time. Pick from available conferencing options or add a custom location.
                </span>
              </tooltip-component>
            </span>
          </label>
          <div class="location-input">
            <select-dropdown
              withSearch={false}
              name="location"
              options={locationOptions}
              defaultSelectedOption={locationOptions.find(i => i.value == this.selectedLocationOption)}
              exportparts="sd__dropdown: nel__dropdown, sd__dropdown-button: nel__dropdown-button, sd__dropdown-content: nel__dropdown-content"
            >
              <span slot="select-icon">{icons[this.selectedLocationOption]}</span>
            </select-dropdown>
            {this.selectedLocationOption === 'custom' && (
              <input type="text" part="nel__location" id="location" name="location" maxlength="1024" value={this.location} onInput={e => this.handleChange(e)} />
            )}
          </div>
        </div>
      </Host>
    );
  }
}
